import React, { Component } from 'react'
import './index.less'
import { Card, List } from 'antd-mobile';
const Item = List.Item;

class Intr extends Component {
  constructor(props, context) {
    super(props)
    this.state = {
      data: [{
        key: "",
        value: ""
      },{
        key: "",
        value: ""
      },{
        key: "",
        value: ""
      }]
    }
  }

  componentDidMount () {

  }



  render() {
    return (
      <div className="intr">
        <div className="header">
          
        </div>
        <div className="block-intr tab-content">
          <Card>
            <Card.Header
              title="AmyChain "
              thumb="./static/logo.png"/>
            <Card.Body>
              <List renderHeader={() => ''}>
                {this.state.data.map(el => {
                  return <div>
                    <Item>{el.key}</Item>
                    <Item multipleLine align="top" wrap>
                      {el.value}
                    </Item>
                  </div>
                })}
              </List>
            </Card.Body>
            <Card.Footer extra={<div style={{color: "#199ed8"}}>AmyChain</div>} />
          </Card>
        </div>
      </div>
    );
  }
}

export default Intr;



