import React, { Component } from 'react'
import './index.less'
import classnames from "classnames";
import { createForm } from 'rc-form';
import AjaxJson from "../utils/ajaxJson"
import { Icon, Button, Card, List } from 'antd-mobile';
const Item = List.Item;

const detail = {
  "id": "1000001",
  category: "001",
  "title": "",
  "img": "insurance0",
  "age": "9060",
  "time": "1",
  "content": "100/"
}

class PolicyDetail extends Component {
  constructor(props, context) {
    super(props)
    this.state = {
    }
  }

  componentWillMount () {
    let param = location.hash.slice(18);
    let index = param.indexOf("&");
    let id = param.slice(0, index);
    this.getPolicyDetail(id);
  }

  //
  getPolicyDetail (id) {
    const _this = this;
    let url = `/api/policies/${id}/detail`;
    let data = {};
    AjaxJson.getResponse(url, data, "GET").then((value) => {
      if(value.status === 2000) {
        value.data.map(el => {
          el.label = el.name;
          el.value = el.orgId;
          return el
        })
        _this.setState({
          orgData: value
        })
      }
    }, (value) => {})
  }

  headerBackClick () {
    window.history.back()
  }

  render() {
    return (
      <div className="flow">
        <div className="header flow-header">
          <div className="header-back" onClick={this.headerBackClick.bind(this)}>
            <Icon type="left" size="lg" />
          </div>
          <div className="header-content"></div>
        </div>
        <div className="flow-content tab-content">
          <Card>
            <Card.Header
              title=""
              thumb="../static/insurance0.jpg"/>
            <Card.Body>
              <List renderHeader={() => ''}>
                <Item></Item>
                <Item multipleLine align="top" wrap>
                  88102
                </Item>
                <Item></Item>
                <Item multipleLine align="top" wrap>
                  3030150%50%
                </Item>
              </List>
            </Card.Body>
            <Card.Footer extra={<div style={{color: "#199ed8"}}></div>} />
          </Card>
        </div>
      </div>
    );
  }
}

export default PolicyDetail;


