import React, { Component } from 'react'
import './index.less'
import classnames from "classnames";
import { createForm } from 'rc-form';
import AjaxJson from "../utils/ajaxJson"
import { Icon, Button, Card, List } from 'antd-mobile';
const Item = List.Item;

const detailList = [{
  "id": "1000001",
  "title": "",
  "age": "9060",
  "time": "1",
  "content": "100/",
  desc: {
    title: "",
    content: [{
      title: "",
      content: "100200"
    },{
      title: "",
      content: ""
    }]
  }
},{
  "id": "1000002",
  "title": "",
  "age": "060",
  "time": "1",
  "content": "608880",
  desc: {
    title: "",
    content: [{
      title: "",
      content: "88"
    },{
      title: "",
      content: ""
    }]
  }
},{
  "id": "1000003",
  "title": "",
  "age": "0-17",
  "time": "30",
  "content": " 10/ ",
  desc: {
    title: "",
    content: [{
      title: "",
      content: "88102"
    },{
      title: "",
      content: "3030150"
    }]
  }
}]

class PolicyDesc extends Component {
  constructor(props, context) {
    super(props)
    this.state = {
      data: ['', '', ''],
      detail: {
        "id": "1000001",
        "title": "",
        "age": "9060",
        "time": "1",
        "content": "100/",
        desc: {
          title: "",
          content: [{
            title: "",
            content: "100200"
          },{
            title: "",
            content: ""
          }]
        }
      },
      index: 0
    }
  }

  componentWillMount () {
    let productId = sessionStorage.getItem("productId");
    if(productId) {
      let detail = "";
      detailList.forEach((el,index) => {
        if(el.id === productId) {
          this.setState({
            detail: el,
            index: index
          })
        }
      })
    }
  }

  headerBackClick () {
    window.history.back()
  }

  saveClick () {
    let policy = {
      base: "",
      product: {
        id: this.state.detail.id,
        category: this.state.detail.detail,
        title: this.state.detail.title,
        content: this.state.detail.content
      },
      promise: "",
      scheduleData: "",
      reportData: "",
    }
    sessionStorage.setItem("policy", JSON.stringify(policy))
    location.hash = "/flow";
  }

  render() {
    return (
      <div className="flow">
        <div className="header flow-header">
          <div className="header-back" onClick={this.headerBackClick.bind(this)}>
            <Icon type="left" size="lg" />
          </div>
          <div className="header-content"></div>
        </div>
        <div className="flow-content tab-content">
          <Card>
            <Card.Header
              title={this.state.detail.title}
              thumb={"./static/insurance" + this.state.index + ".jpg"}/>
            <Card.Body>
              <List renderHeader={() => this.state.detail.desc.title}>
                {this.state.detail.desc.content.map(el => {
                  return <div>
                    <Item>{el.title}</Item>
                    <Item multipleLine align="top" wrap>
                      {el.content}
                    </Item>
                  </div>
                })}
              </List>
            </Card.Body>
            <Card.Footer extra={<div style={{color: "#199ed8"}}></div>} />
          </Card>
          <Button style={{margin: "2rem"}} type="ghost" onClick={this.saveClick.bind(this)}></Button>
        </div>
      </div>
    );
  }
}

export default PolicyDesc;


